ems:center;padding:0 16px;cursor:pointer;-webkit-transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)}.material-select-option:hover{background-color:#EEEEEE}.material-select-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:5}



